.footer {
    display: flex;
    background-color: var(--primary);
    padding: 15px;
    border-top: 1px solid var(--grey-main);
    color: var(--grey-dark);
    justify-content: space-between;
    
}

.footer a{
    text-decoration: underline;
}

.footer a:hover{
    text-decoration: none;
}